@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.app{height:100vh;overflow-y:scroll;background:url(/admin-panel/static/media/hero_bg.0540b794.jpg) center/cover no-repeat}.content{display:grid;grid-template-columns:560px 440px;max-width:1040px;margin:0 auto;padding-top:50px}.content__interactive{padding-left:40px}
.csstransition-enter{opacity:0;transform:scale(0.9)}.csstransition-enter-active{opacity:1;transform:translateX(0);transition:opacity 300ms,transform 300ms}.csstransition-exit{opacity:1}.csstransition-exit-active{opacity:0;transform:scale(0.9);transition:opacity 300ms,transform 300ms}*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto",sans-serif}ul{padding:0}
